<template>
	<div class="about">
		<div class="about-1">
			<div class="left">
				<h2>
					<i class="iconfont icon-xiaoxizhongxin"></i>
					<span>消息中心</span>
				</h2>
				<br>
				<ul>
					<li @click="AboutMe" :class="{pitch:tabcomponents==='AboutMe'}">关于我</li>
					<li @click="Four" :class="{pitch:tabcomponents==='Four'}">404页面</li>
					<li @click="ThemeSettings" :class="{pitch:tabcomponents==='ThemeSettings'}">主题设置</li>
				</ul>
			</div>
			<div class="right">
				<header><h2>{{tabcomponents}}</h2></header>
				<section>
					<components :is="tabcomponents">
					</components>
				</section>
			</div>
			
			<div class="SidebarButton" :class="{rotate0: rotateCut}"  >
				<div class="buttonswitch">
					<div class="switchanimate" :class="{switchanimateclick: rotateCut}"></div>
				</div>
				<ul ref="buttonswitchREF"  @click.self="switchFun">
					<li @click="AboutMe">
						<i class="iconfont icon-guanyuwomen"></i>
					</li>
					<li @click="Four">
						<i class="iconfont icon-zhuti1"></i>
					</li>
					<li @click="ThemeSettings">
						<i class="iconfont icon-icon-test"></i>
					</li>
					
				</ul>
			</div>
		</div>
		
	</div>
</template>
<script>
	import AboutMe from "@/components/aboutme/AboutMe.vue";
	// import Message from "@/components/message/Message.vue";
	import Four from "@/components/404/404.vue";
	import ThemeSettings from "@/components/themesettings/ThemeSettings.vue";
	export default{
		name: "About",
		methods:{
			AboutMe: function(){
				return this.tabcomponents = "AboutMe";
			},
			Four: function(){
				return this.tabcomponents = "Four";
			},
			ThemeSettings: function(){
				return this.tabcomponents = "ThemeSettings";
			},
			RadiusFun: function(li){
				const GetLi = li
				let Radius = 120;
				const LiLength = GetLi.length
				GetLi.forEach((item,index)=>{
					let Angle = ( (180 - 15 * (LiLength)) / LiLength) * index * Math.PI / 180
					item.style = `
						left:${Math.sin(Angle) * Radius}px;
						top:${Math.cos(Angle) * Radius}px;
					`;
					console.log(120 + 15 * LiLength);
				});
			},
			switchFun: function(){
				this.rotateCut = !this.rotateCut;
				console.log(this.rotateCut);
			}
		},
		components:{
			AboutMe,
			Four,
			ThemeSettings
		},
		data() {
			return {
				tabcomponents: "AboutMe",
				rotateCut: false
			}
		},
		mounted() {
			const li = this.$refs.buttonswitchREF.childNodes;
			// const li = this.$refs.buttonswitchREF
			console.log(li);
			this.RadiusFun(li)
		}
	}
</script>
<style lang="less">
.about {
	background: url(https://s2.hdslb.com/bfs/static/blive/blfe-message-web/static/img/infocenterbg.a1a0d152.jpg) top/cover no-repeat fixed;
	z-index: 2;
	// min-height: 88vh;
	display: flex;
	justify-content: center;
	overflow-x: hidden;

	.about-1 {
		display: flex;
		flex-direction: row;
		margin: 0 auto;
		width: 60%;
		min-width: 980px;

		.left {
			flex: 1;
			max-width: 140px;
			// background-color: rgba(255, 255, 255, 0.8);
			background-color: var(--about-bgcolor);
			
			h2{
				height: 62px;
				display: flex;
				justify-content: center;
				align-items: center;
				// color: #333;
				color: var(--about-h2);
			}
			ul{
				display: flex;
				flex-direction: column;
				text-align: left;
				text-indent: 3.2em;
				list-style-type: unset;
				li{
					padding:  1em 0;
					// color: #6b757b;
					color: var(--about-li);
					cursor: pointer;
				}
				.pitch{
					// 点击切换的颜色
					// color: #2faee3;
					color: var(--about-click);
				}
			}
		}
		.right {
			flex: 1;
			width: calc(100% - 140px);
			// background-color: rgba(255, 255, 255, 0.5);
			background-color: var(--about-bgcolor2);
			display: flex;
			flex-direction: column;
			align-items: center;
			border-radius: 4px;
			height: calc(100vh - 104px);
			header{
				width: 98%;
				// background-color: #fff;
				background-color: var(--about-headerbg);
				text-indent: 2em;
				display: flex;
				align-items: center;
				line-height: 4em;
				border-radius: 4px;
				margin: 1em 0;
				h2{
					font-size: 15px;
					// color: #666;
					color: var(--about-headerh2);
				}
			}
			section{
				width: 98%;
				border-radius: 4px;
				// background-color: #fff;
				background-color: var(--about-section);
				overflow-y: auto;
			}
			
		}
		.SidebarButton{
			display: none;
		}
	}
}
@media screen and (max-width: 1100px) {
	body{
		overflow-x: hidden;
	}
	.about {
		.about-1 {
			// width: 100%;
			min-width: 100%;
			.left {
				display: none;
			}
			.right {}
			.rotate0{
				-webkit-transform: rotate(0deg)!important;
				-moz-transform: rotate(0deg)!important;
				-ms-transform: rotate(0deg)!important;
				-o-transform: rotate(0deg)!important;
				transform: rotate(0deg)!important;
			}
			.SidebarButton{
				position: fixed;
				bottom: 5%;
				right: 0%;
				background-color: #3498db;
				width: 50px;
				height: 50px;
				border-radius: 50%;
				box-shadow: 0 0 12px 3px rgba(255, 255, 255, 0.9);
				display: block;
				cursor: pointer;
				-webkit-transform: rotate(180deg);
				-moz-transform: rotate(180deg);
				-ms-transform: rotate(180deg);
				-o-transform: rotate(180deg);
				transform: rotate(180deg);
				-webkit-transition: all .5s;
				-moz-transition: all .5s;
				-ms-transition: all .5s;
				-o-transition: all .5s;
				transition: all .5s;
				ul{
					width: 50px;
					height: 50px;
					position: relative;
					top: -50px;
					transform: rotate(180deg);
					li{
						width: 50px;
						height: 50px;
						border-radius: 50%;
						background-color: #3498db;
						display: flex;
						justify-content: center;
						align-items: center;
						position: absolute;
						transform: rotate(180deg);
						i{
							color: #fff;
						}
					}
				}
				.buttonswitch{
					// 变化前
					width: 100%;
					height: 100%;
					
					&:active{
						border-radius: 50%;
						box-shadow: 0 0 10px 0px rgba(255, 255, 255, 0.9);
					}
					.switchanimate{
						transition: all 1s ease;
						width: 28px;
						height: 3px;
						background: #fff;
						position: absolute;
						-webkit-transform-origin: 0 50%;
						transform-origin: 0 50%;
						top: 35%;
						margin-top: 14.5px;
						left: 40px;
						transform: rotate(180deg);
						
						&::before{
							content: "";
							transition: all 1s ease;
							width: 28px;
							height: 3px;
							background: #fff;
							position: absolute;
							-webkit-transform-origin: 0 50%;
							transform-origin: 0 50%;
							margin-top: 30%;
						}
						&::after{
							content: "";
						    transition: all 1s ease;
						    width: 28px;
						    height: 3px;
						    background: #fff;
						    position: absolute;
						    -webkit-transform-origin: 0 50%;
						    transform-origin: 0 50%;
							margin-top: 60%;
						}
					}
					.switchanimateclick{
						// 点击后
						background: transparent;
						&::before{
							content: "";
							transform: rotate(45deg) translate(-3px, -11px);
						}
						&::after{
							content: "";
							transform: rotate(-45deg) translate(4px, 4px);
						}
					}
				}
			}
		}
	}
}
</style>
