<template>
  <div class="popup-window">
    <div class="popup"

	v-for="(item,index) in popup"
	:key="item.time"
         :class="{'depleted': item.end === 1}"
	>
      <p>
        <i class="iconfont icon-chenggong"></i>
        <span>{{ item.state }}</span>
        <strong>{{ item.txt }} </strong>
        <i class="iconfont icon-guanbi-copy cursor" v-on:click="deletearray(index)"></i>
      </p>
      <div class="reading" v-on:animationend="animationendFun(index,500)"></div>
    </div>
  </div>

</template>
<script>
	export default{
	  name: "PopupWindow",
		props:['popup'],
		methods:{
      animationendFun: async function(index,timeout=500){
        await this.$set(this.popup[index],"end",1);
        setTimeout(()=>{
          this.deleteFun(index)
        },timeout)
      },
      deleteFun: function (){
        this.popup.filter((item,index,array)=>{
          if(item.end ===1){
            array.splice(index,1)
          }
        })
			},
      deletearray: function (index){
        this.animationendFun(index,0)
      }
		},
	}
</script>
<style lang="less" scoped>
.popup-window {
  position: fixed;
  right: 10px;
  bottom: 40px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  transition: all .5s;
  & > .popup {
    // background-color: rgba(230, 126, 34,1.0);
	background-color: var(--themebackground);
    display: inline-flex;
    position: relative;
    animation: popupmenu .5s ease-in-out forwards;
    transition: all .5s;
    margin: .2em 0;
	  height: 0px;

    p {
      display: flex;
      align-items: center;
      line-height: 3em;
      padding: 0 2em;
	  
      span,
      i,
      strong {
        // color: #ecf0f1;
		color: --themeblue;
      }
      span{
        animation: leftFun .2s linear;
      }
      i{
        margin: 0 1em;
      }
      i{
        animation: optica .2s linear;
      }
      .cursor {
        cursor: pointer;
      }
    }
    .reading {
      position: absolute;
      width: 100%;
      height: 2px;
      // background-color: rgba(241, 196, 15,1.0);
	  background-color: var(--themebackground);
      bottom: 0;
      left: 0;
      animation: longtime 4s linear forwards;
    }

  }
  .popup:hover>.reading {
    animation-play-state: paused;
  }
  //.popup:last-of-type{
  //  position: absolute;
  //}
  .enter{
    animation: popupmenu .2s linear;
  }
  .depleted{
    animation: deletion .8s linear forwards;
  }
}

@keyframes longtime {
  0%{
    width: 100%;
  }
  100%{
    width: 0;
  }
}
@keyframes popupmenu {
  0%{
    transform: translate(30px,20px);
    opacity: .2;
  }
  25{
	  transform: translate(10px,10px);
	  opacity: 0;
    height: 42px;
  }
  50%{
    transform: translate(10px,0);
    opacity: .5;
    height: 42px;
  }
  75%{
  	transform: translate(20px,0);
    opacity: .7;
    height: 42px;
  }
  100%{
      height: 42px;
  }
}
@keyframes deletion {
  0%{}
  25%{
    transform: translate(0px,-20px);
    opacity: .5;
  }
  50%{
    transform: translate(0,-10px);
    opacity: .2;
  }
  100%{
    transform: translate(0,10px);
    opacity: 0;
    height: 0;
  }
}
@keyframes leftFun {
  0%{
    opacity: .4;
  }
  25%{
    transform: translate(20px, 0);
  }
  50%{
    transform: translate(10px, 0);
  }
  100%{
    transform: translate(0px, 0);
  }
}
@keyframes optica {
  100%{
    opacity: .8;
  }
}
</style>