<template>
  <div class="typesetting">
    <div class="change">
      <button class="ripple"  @click="ripple($event)">
        <span>网站字体颜色更改</span><i class="iconfont icon-zitiyanse"></i>
		<span></span>
      </button>
    </div>
	<popup-window :popup="popup"></popup-window>
  </div>
</template>
<script>
	import PopupWindow from "@/components/pop-up-window/PopupWindow.vue"
	export default{
		name: "typesetting",
		data(){
			return{
				popup:[]
			}
		},
		methods:{
			ripple: function(e){
				const x = e.clientX - e.target.offsetLeft;
				const y = e.clientY - e.target.offsetTop;
				e.target.lastChild.style = `top:${y}px;left:${x}px;`;
				e.target.lastChild.className = `ripple-color`;
				setTimeout(()=>{
					e.target.lastChild.className = ''
				},200);
				this.callbackPopup("ojbk","修改颜色成功=。=");
				this.colorFun()
			},
			callbackPopup:async function(state, txt,){
				let times = await Date.now()
				this.popup.push({
					state: state,
					txt: txt,
					time:times
				});
			},
      colorFun: function (){
        const ThemeWords = {
			  color1: "#1abc9c",
			  color2: "#c0392b",
			  color3: "#d35400",
			  color4: "#f39c12",
			  color5: "#f1c40f",
			  color7: "#8e44ad",
			  color6: "#3498db"
        }
        window.sessionStorage.setItem("theme",JSON.stringify(ThemeWords,null,2));
        console.log(window.sessionStorage.getItem("theme"));
		const rootref = document.querySelector(":root");
		rootref.style.setProperty("--themeblue","rgba(230, 126, 34,1.0)");
		rootref.style.setProperty("--themebackground","rgba(236, 240, 241,1.0)");
		rootref.style.setProperty("--bgimage","url(../img/bg_01.jpg)");
      }
		},
		components:{
			PopupWindow
		}
	}
</script>
<style lang="less" scoped>
.typesetting {
  padding: 1em;
  .change {
    background: #fff;
    width: 100%;
    margin: 1em 0;
    .ripple {
      will-change: box-shadow;
      background-color: rgb(0, 168, 255);
      box-shadow: 0 0 7px 1px rgb(64, 115, 158);
      padding: 1em;
      margin: 0 1em;
      border-radius: 4px;
      position: relative;
      overflow: hidden;
      span,
      i {
        pointer-events: none;
      }
      span {
        // color: rgb(245, 246, 250);
		color: var(--themeblue);
      }
      i {
        // color: rgb(194, 54, 22);
		color: var(--themeblue);
      }

    }
    .ripple:hover {
      background-color: rgb(156, 136, 255);
    }
    .ripple:active {
      background-color: rgb(251, 197, 49);
      box-shadow: none;
    }
  }
}

.none{
	display: none;
}
.ripple-color {
	  animation: ripple 0.2s linear;
	  position: absolute;
	  top: 50%;
	  left: 50%;
	  margin-right: -50%;
	  opacity: 0.9;
	  transform: translate(-50%, -50%);
	  width: 10px;
	  height: 10px;
	  background-color: #2c3e50;
	  opacity: 0;
	  border-radius: 50%;
	  display: block;
}
@keyframes ripple {
  100% {
    transform: scale(5);
    background-color: transparent;
	opacity: .7;
	width: 100px;
	height: 100px;
  }
}
</style>